.logo {
  font-family: myriad pro, Verdana, Arial, Helvetica, sans-serif;
  font-size: 28px;
  text-transform: uppercase;
  color: #000;
  letter-spacing: 4px;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
  user-select: none;
  text-shadow: 1px 1px 0 #ccc, 2px 2px 0 #ccc, 3px 3px 0 #444, 4px 4px 0 #444, 5px 5px 0 #444, 6px 6px 0 #444, 5px 5px 15px #999;
}
