.container {
  max-width: 1020px;
  margin: 0 auto;
  .header {
    text-align: center;
    padding: 16px 0 32px;
    border-bottom: 1px solid #eee;
    margin: 0 32px 16px;
    h1 {
      margin: 0;
      color: #444;
      font-weight: 300;
      font-size: 48px;
    }
    h2 {
      margin: 0;
      color: #666;
      font-weight: 300;
      font-size: 20px;
    }
  }
  .translate {
    display: flex;
    .wrap {
      flex: 1;
      margin: 16px 32px;
      position: relative;
      h3 {
        font-size: 20px;
        margin: 0 0 4px;
        font-weight: 600;
      }
      .textArea {
        resize: none;
        height: auto;
        padding: 12px 16px;
        font-size: 20px;
        border-radius: 2px;
      }
      .btn {
        position: absolute;
        bottom: 8px;
        right: 8px;
      }
      :global {
        .ant-btn {
          border-radius: 2px;
        }
        .ant-btn-default.ant-btn-background-ghost {
          color: #aaa;
          border-color: #aaa;
          &:hover {
            color: #4096ff;
            border-color: #4096ff;
          }
        }
      }
    }
  }
}
